html:is(.dark) {
    .popularList {
 &::-webkit-scrollbar {
  width: 6px;
  height: 8px;
}

&::-webkit-scrollbar-thumb {
  background: rgb(179, 179, 179);
  border-radius: 30px;
}

&::-webkit-scrollbar-thumb:hover {
background:white;
}

&::-webkit-scrollbar-track {
  background: transparent;
}
}

  .loader {
    color: white;
  }
  body {
    background-color: color('darkModePrimary');
    color:white;
  }
 
  .nav,.nav_items,footer {
    background-color:color('darkModeSecondary');
    color: white;
    .goUp {
      background-color: black;
    }
  } 
  .logo {
    color: color('coffee');
    &::after,
    &::before {
    background-color: color('coffee');
    }
  }
  
  .nav_items {
      a {
      color: white;
      }
     }
    
     .activeNav {
      .toggle_nav{
       .toggle_items:nth-child(2) {
        @include transition(300ms);
        position: absolute;
        width: 50px;
        height: 50px;
        padding: 20px;
        right: 13.9px;
        border-radius: 50%;
        background-color: transparent;
        // opacity: 30%;
        border: 4px dashed white;
        animation: rollBorderWhite 5s ease-in-out infinite;
      }
     }
    }
  .toggle_items {
        background-color: color('coffee');
      }
    .subheading, .heading {
    color: white;
  }

 .card {
  
     background-color: color('black');
  
  
      &:hover {
    &::after {
    //   background:color('wheat');
    background-color: white;
    }
  } 
    .body {
       background-color: color('darkModeSecondary');
       .address {
        &:after {
          background-color: color('coffee');
        }
       }
    }
  .school {
    color: white;
    background-color: rgba(78, 78, 78, 0.546);
    }
  
}
.agentCardName {
  color: color('coffee')
}
.popularCard, .pagination {
  background-color: color('darkModeSecondary');
}


.single_card, .user-card {
  background-color: color('darkModeSecondary');
}


  .agentProfile,.listingHistory, .availableLists
   {
     background-color: color('darkModeSecondary');
     color: white;
